<template>
  <div id="applay_scroll">
    <!-- 员工审核 -->
    <div
      v-for="(item,i) in applyList"
      :key="i"
      class="staff"
      @click="goNext('/staff-info',item)"
    >
      <div class="staff-left">
        <img
          class="head-img"
          :src="item.applyUser.headUrl"
          alt=""
        >
        <div>
          <div>{{ item.applyUser.realName || item.applyUser.nickName || '暂无' }}</div>
          <div class="a5">{{ item.applyUser.phone }}</div>
        </div>
      </div>
      <div class="staff-right">
        <div>审核中</div>
        <van-icon
          class="right-img"
          name="arrow"
        />
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessAuditApprovalUnapprovedByhotel } from '@/api/api_staff'
export default {
  data() {
    return {
      applyList: []
    };
  },
  created() {
    this.$store.commit('setHeader', '员工审核');
    this.getInfo()
  },
  methods: {
    getInfo() {
      GetBusinessAuditApprovalUnapprovedByhotel({ hotelId: this.$route.query.hotelId }).done(res => {
        this.applyList = res.data
      })
    },
    // 申请页面
    goNext(url, item) {
      let query = {
        hotelId: this.$route.query.hotelId,
        auditApprovalId: item.id,
        userId: item.applyUserId,
        name: item.applyUser.realName || item.applyUser.nickName,
        headUrl: item.applyUser.headUrl,
        phone: item.applyUser.phone,
      }
      this.$router.push({
        path: url,
        query: query
      })
      localStorage.setItem('staffInfo', JSON.stringify(query))
    }
  }
};
</script>
<style scoped>
#applay_scroll {
    margin: 10px 0;
    overflow: auto;
    height: calc(100vh - 70px);
}
.staff {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    font-size: 0.8rem;
    background: #fff;
    border-bottom: 1px solid #f2f2f2;
}
.staff-left {
    display: flex;
    text-align: left;
}
.staff-right {
    display: flex;
    align-items: center;
}
.head-img {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin-right: 10px;
}
.f10 {
    font-size: 0.6rem;
    color: #5c5c5c;
    border: 1px solid #d2d0d3;
    margin: 0 2px;
    padding: 2px;
    border-radius: 6px;
}
.right-img {
    margin-left: 10px;
    color: #989898;
}
.a5 {
    color: #a5a5a5;
}
</style>



